import React, { Component } from 'react';
import Thanhpho from './Thanhpho';

class Japan extends Component {
    constructor(props){
        super(props);
        this.state = {
            reponse : {
                "message": null,
                "result": [{
                    "prefCode": 1,
                    "prefName": "北海道"
                }],
            },
            check : true,
        }
    }

    
    componentDidMount() {
        fetch("https://opendata.resas-portal.go.jp/api/v1/prefectures",{
            headers: { "X-API-KEY": "tlaN2LWtVbBqDgnuoA75AuMoNCz51mLus5evJUpW"},
        })
        .then(res => res.json())
        .then(
            result => {this.setState({reponse : result})},
        )
    }

    handleChange = (data,danso,check) => {
        this.props.onHandleChange(data,danso,check);
    }

    handleChangeThanhPho = (check) => {
        this.props.onHandleChangeThanhPho(check);
    }

    handleChangeJapan = (check) => {
        if(check){
            this.setState({check : true});
        }else{
            this.setState({check : false});
        }
    }

    render() {
        return (
            <div className="checkbox">
                <h1 className="h1allThanhPho"> </h1>
                <button className="btn btn-primary" onClick={() => {
                    if(this.state.check){
                        document.querySelector('.allThanhPho').style.display = 'grid';
                    }else{
                        document.querySelector('.allThanhPho').style.display = 'block';
                    }
                }} href="/" data-toggle="modal" data-target="#myModal">都市を選択してください</button>
                <div className="allThanhPho modal fade" id="myModal" role="dialog">
                    <button type="button" className="close" data-dismiss="modal">&times;</button>
                    {this.state.reponse.result.map((item,index) => {
                        return <Thanhpho onHandleJapan={this.handleChangeJapan} onHandleChange={this.handleChange} onHandleChangeThanhPho={this.handleChangeThanhPho} index={item.prefCode} key={index}>{item.prefName}</Thanhpho>
                    })}
                </div>
            </div>
        );
    }
}

export default Japan;